  }
  /* contact form */

  input[type=text], select, textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
    resize: vertical;
  }
  .myform{
    margin: auto;
    border: 3px  #ff4040;
    align-self: center;
    width: 40%;
    flex: center 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 100vh;
  
  
  }
  .mybtn{
    color: black;
    font-size: 20px;
  
    transition-duration: 0s;
  
  
  
  }
  .courseImg{
     height:50%;
     width: 40%;
  }
  .mybtn:hover{
    background-color: rgb(67, 226, 141) ;
    color:black;
